import React from 'react'
import { Grid } from '@material-ui/core'
import { makeStyles } from '@material-ui/core';

import GridPage from '../../components/GridPage'
//import Detail from '../../components/Detail'

function createData(id, name, commodity, suitability) {
  return { id, name, commodity, suitability };
}

const rows = [
  createData('1', 'palm', 'Inceptisol', 'yes'),
  createData('2', 'palm', 'Oxisol', 'yes'),
  createData('3', 'palm', 'Alfisol', 'yes'),
  createData('4', 'palm', 'Ultisol', 'yes'),
  createData('5', 'palm', 'Spodosol', 'yes'),
  createData('6', 'palm', 'Entisol', 'yes'),
  createData('7', 'palm', 'Histosol', 'no'),
];

const headCells = [
  { id: 'id', numeric: false, disablePadding: true, label: 'Sequence' },
  { id: 'name', numeric: false, disablePadding: true, label: 'Type' },
  { id: 'commodity', numeric: false, disablePadding: true, label: 'Commodity' },
  { id: 'suitability', numeric: false, disablePadding: true, label: 'Suitability' },
];

const datatheme='Soil';

const useStyles = makeStyles({
    appHeader: {
      paddingLeft: '10px',
      paddingRight: '10px',
      width: '100%',
    },
    appMaster: {
      paddingLeft: '10px',
      paddingRight: '10px',
      width: '100%',
      maxHeight: '500px',
    },
    appDetail: {
      paddingLeft: '10px',
      width: '100%',
      verticalAlign: 'top',
    }
})

function SoilType() {
  const classes = useStyles()
  return (
    <>
      <Grid container className={classes.appDetail}>
          <Grid className={classes.appHeader} sm={6}>
          <GridPage rows={rows} headCells={headCells} datatheme={datatheme} />
          </Grid>
          <Grid className={classes.appHeader} sm={6}>
          </Grid>
      </Grid>
    </>
  )
}

export default SoilType;