import React from 'react'
import { Grid } from '@material-ui/core'
import { makeStyles } from '@material-ui/core';

import GridPage from '../GridPage'
//import Detail from '../Detail'

function createData(id, name, variety) {
  return { id, name, variety };
}

const rows = [
  createData('1', 'palm', 'dxpsimalungun'),
  createData('2', 'palm', 'lonsum'),
  createData('3', 'palm', 'ddmarihat'),
  createData('4', 'pineapple', 'queen'),
  createData('5', 'pineapple', 'cayene'),
];

const headCells = [
  { id: 'id', numeric: false, disablePadding: true, label: 'Sequence' },
  { id: 'name', numeric: false, disablePadding: true, label: 'Commodity' },
  { id: 'variety', numeric: false, disablePadding: true, label: 'Variety' },
];

const datatheme='Varieties';

const useStyles = makeStyles({
    appHeader: {
      paddingLeft: '10px',
      paddingRight: '10px',
      width: '100%',
    },
    appMaster: {
      paddingLeft: '10px',
      paddingRight: '10px',
      width: '100%',
      maxHeight: '500px',
    },
    appDetail: {
      paddingLeft: '10px',
      width: '100%',
      verticalAlign: 'top',
    }
})

function Variety() {
  const classes = useStyles()
  return (
    <>
      <Grid container className={classes.appDetail}>
          <Grid className={classes.appHeader} sm={6}>
          <GridPage rows={rows} headCells={headCells} datatheme={datatheme} />
          </Grid>
          <Grid className={classes.appHeader} sm={6}>
          </Grid>
      </Grid>
    </>
  )
}

export default Variety;