import React from 'react';
//import { Planet } from 'react-planet';
import { Router, Switch, Route } from "react-router-dom";

//import MainMenuIcon from '@material-ui/icons/Apps';
//import MasterDataIcon from '@material-ui/icons/Storage';
//import CompanyAssetIcon from '@material-ui/icons/FolderSpecial';
//import CultivationStandardIcon from '@material-ui/icons/Assignment';
//import AssetDistributionIcon from '@material-ui/icons/AccountTree';
//import DataTransactionIcon from '@material-ui/icons/Tune';

import './index.css'
import history from './history';

import Header from "./components/Header";
import Main from "./components/MainMenu";

import Commodity from "./components/MasterData/Commodity";
import Variety from "./components/MasterData/Variety";
import AssetGroup from "./components/MasterData/AssetGroup";
import AssetClass from "./components/MasterData/AssetClass";
import AssetStatus from "./components/MasterData/AssetStatus";
import Construction from "./components/MasterData/Construction";
import Direction from "./components/MasterData/Direction";
import SoilType from "./components/MasterData/SoilType";
import UnitMesasurement from "./components/MasterData/UnitMesasurement";
import UnitConverter from "./components/MasterData/UnitConverter";

import Infrastructure from "./components/CompanyAsset/Infrastructure";
import Landstructure from "./components/CompanyAsset/Landstructure";
import TreeManagement from "./components/CompanyAsset/TreeManagement";
import Fleet from "./components/CompanyAsset/Fleet";

import CultivationStandard from "./components/CultivationStandard/CultivationStandard";
import AssetDistribution from "./components/AssetDistribution/AssetDistribution";

import InfrastructureTransaction from "./components/DataTransaction/Infrastructure";
import LandstructureTransaction from "./components/DataTransaction/Landstructure";
import TreeManagementTransaction from "./components/DataTransaction/TreeManagement";
import FleetTransaction from "./components/DataTransaction/Fleet";

//const useStyles = makeStyles({
//    appPlanet: {
//      paddingLeft: '10px',
//      paddingRight: '10px',
//      justifyContent:"center",
//      verticalAlign: "top",
//      width: '100%',
//    }
//  })

function App() {
    //const classes = useStyles()
    return (
        <>
        <div>
            <div>
                <Header />
            </div>
                <Router history={history}>
                    <Switch>
                        <Route path="/" exact component={Main} />

                        <Route path="/Commodity" component={Commodity} />
                        <Route path="/Variety" component={Variety} />
                        <Route path="/AssetGroup" component={AssetGroup} />
                        <Route path="/AssetClass" component={AssetClass} />
                        <Route path="/AssetStatus" component={AssetStatus} />
                        <Route path="/Construction" component={Construction} />
                        <Route path="/Direction" component={Direction} />
                        <Route path="/SoilType" component={SoilType} />
                        <Route path="/UnitMesasurement" component={UnitMesasurement} />
                        <Route path="/UnitConverter" component={UnitConverter} />

                        <Route path="/Infrastructure" component={Infrastructure} />
                        <Route path="/Landstructure" component={Landstructure} />
                        <Route path="/TreeManagement" component={TreeManagement} />
                        <Route path="/Fleet" component={Fleet} />

                        <Route path="/CultivationStandard" exact component={CultivationStandard} />
                        <Route path="/AssetDistribution" component={AssetDistribution} />


                        <Route path="/InfrastructureTransaction" component={InfrastructureTransaction} />
                        <Route path="/LandstructureTransaction" component={LandstructureTransaction} />
                        <Route path="/TreeManagementTransaction" component={TreeManagementTransaction} />
                        <Route path="/FleetTransaction" component={FleetTransaction} />
                    </Switch>
                </Router>
        </div>
        </>
	);
}

export default App;