import React from 'react'
import { AppBar, Tabs, Tab, makeStyles } from '@material-ui/core'
import SettingsIcon from '@material-ui/icons/Settings';
import Table from './Table'
//import { Router } from '@material-ui/icons';
//import { Link, Route } from 'react-router-dom';

const useStyles=makeStyles({
    appSideMenu: {
        paddingLeft: '10px',
        width: '100%',
        maxHeight: '200px',
        overflow: 'auto',
        paddingTop: '5px',
        paddingbottom: '5px',
    },
    appDetailBar: {
        paddingRight: '10px',
        width: '100%',
        maxHeight: '70px',
    },      
})

//export default function Sidemenu(props) {
const Sidemenu = (props) => {
    const classes = useStyles()
    /*
   const characters = [
      {
        date: '2 Oct 2020',
        activity: 'Penanaman',
      },
      {
        date: '4 Oct 2020',
        activity: 'Pemupukan',
      },
      {
        date: '11 Oct 2020',
        activity: 'Pemangkasan',
      },
      {
        date: '14 Oct 2020',
        activity: 'Pengamatan',
      },
      {
        date: '16 Oct 2020',
        activity: 'Pengendalian Hama',
      },
      {
        date: '16 Oct 2020',
        activity: 'Pengendalian Penyakit',
      },
    ]
    */
   const {rowList,colHead} = props
    const tablegroup = [
        {
          groupname: 'Activities',
        },
    ]
    return (
        <>
            <AppBar position="static" className={classes.appDetailBar}>
                <Tabs variant="fullWidth">
                <Tab icon={<SettingsIcon />} label="Acitivities" />
                </Tabs>
            </AppBar>
            <div className={classes.appSideMenu}>
                <Table
                    characterHead={colHead}
                    characterData={rowList}
                    tableGroup={tablegroup}
                />
           </div>
        </>
    )
}

export default Sidemenu