import React from 'react'
import { Grid } from '@material-ui/core'
import { makeStyles } from '@material-ui/core';

import GridPage from '../../components/GridPage'
//import Detail from '../../components/Detail'

function createData(id, name, degree) {
  return { id, name, degree };
}

const rows = [
  createData('1', 'north', 0),
  createData('2', 'northeast', 45),
  createData('3', 'east', 90),
  createData('4', 'southeast', 135),
  createData('5', 'south', 180),
  createData('6', 'southwest', 225),
  createData('7', 'West', 270),
  createData('8', 'northwest', 315),
];

const headCells = [
  { id: 'id', numeric: false, disablePadding: true, label: 'Sequence' },
  { id: 'name', numeric: false, disablePadding: true, label: 'Direction' },
  { id: 'degree', numeric: true, disablePadding: true, label: 'Degree' },
];

const datatheme='Direction';

const useStyles = makeStyles({
    appHeader: {
      paddingLeft: '10px',
      paddingRight: '10px',
      width: '100%',
    },
    appMaster: {
      paddingLeft: '10px',
      paddingRight: '10px',
      width: '100%',
      maxHeight: '500px',
    },
    appDetail: {
      paddingLeft: '10px',
      width: '100%',
      verticalAlign: 'top',
    }
})

function Direction() {
  const classes = useStyles()
  return (
    <>
      <Grid container className={classes.appDetail}>
          <Grid className={classes.appHeader} sm={6}>
          <GridPage rows={rows} headCells={headCells} datatheme={datatheme} />
          </Grid>
          <Grid className={classes.appHeader} sm={6}>
          </Grid>
      </Grid>
    </>
  )
}

export default Direction;