import { Grid, makeStyles } from "@material-ui/core";
//import { FormatBold, FormatSize } from "@material-ui/icons";
import React from "react";
import history from '../history';

import HomeIcon from '@material-ui/icons/Home';

const useStyle = makeStyles({
    appHeader: {
        textAlign:"center",
        fontSize: "20px",
        fontWeight: "bold",
        color: "#00BB88",
    }
})
function Header() {
    const classes = useStyle()
      return (
          <div className={classes.appHeader}>
              <Grid container>
                <Grid item xs={9}>
                Thanduran: Main Menu
                </Grid>
                <Grid item xs={3}>
                <div onClick={() => history.push('/')} title={"Home"}><HomeIcon /></div>
                </Grid>
            </Grid>
          </div>
      );
  }

  export default Header;