import React from 'react'
import { Grid } from '@material-ui/core'
import { makeStyles } from '@material-ui/core';

import GridPage from '../GridPage'
//import Detail from '../Detail'

function createData(id, code, title) {
  return { id, code, title };
}

const rows = [
  createData('1', 'inres0001', 'Penampungan Air'),
  createData('2', 'inset0001', 'Pemukiman'),
  createData('3', 'infas0001', 'Fasilitias Umum'),
  createData('4', 'inoff0001', 'Kantor'),
  createData('5', 'infac0001', 'Pabrik Kelapa Sawit'),
  createData('6', 'inwrh0001', 'Gudang'),
  createData('7', 'plantsi', 'Kebun Sawit Inovatif'),
  createData('8', 'divsi01', 'Division 01'),
  createData('9', 'divsi02', 'Division 02'),
  createData('10', 'divsi03', 'Division 03'),
  createData('11', 'blksi01001', 'Block 01/001'),
  createData('12', 'blksi01002', 'Block 01/002'),
  createData('13', 'blksi01003', 'Block 01/003'),
  createData('14', 'blksi02001', 'Block 02/001'),
  createData('15', 'blksi02002', 'Block 02/002'),
  createData('16', 'blksi02003', 'Block 02/003'),
  createData('17', 'blksi03001', 'Block 03/001'),
  createData('18', 'blksi03002', 'Block 03/002'),
  createData('19', 'blksi03003', 'Block 03/003'),
];

const headCells = [
  { id: 'id', numeric: false, disablePadding: true, label: 'Sequence' },
  { id: 'code', numeric: false, disablePadding: true, label: 'Code' },
  { id: 'title', numeric: false, disablePadding: true, label: 'Title' },
];

const datatheme='Land Structures';

const useStyles = makeStyles({
    appHeader: {
      paddingLeft: '10px',
      paddingRight: '10px',
      width: '100%',
    },
    appMaster: {
      paddingLeft: '10px',
      paddingRight: '10px',
      width: '100%',
      maxHeight: '500px',
    },
    appDetail: {
      paddingLeft: '10px',
      width: '100%',
      verticalAlign: 'top',
    }
})

function Landstructure() {
  const classes = useStyles()
  return (
    <>
      <Grid container className={classes.appDetail}>
          <Grid className={classes.appHeader} sm={6}>
          <GridPage rows={rows} headCells={headCells} datatheme={datatheme} />
          </Grid>
          <Grid className={classes.appHeader} sm={6}>
          </Grid>
      </Grid>
    </>
  )
}

export default Landstructure;