import React from 'react'
import { Grid } from '@material-ui/core'
import { makeStyles } from '@material-ui/core';

import GridPage from '../GridPage'
//import Detail from '../Detail'

function createData(id, code, title) {
  return { id, code, title };
}

const rows = [
  createData('1', 'inrod0001', 'Jalan blok SI 01/001'),
  createData('2', 'inbrd0001', 'Jembatan Titian'),
  createData('3', 'inwbr0001', 'Jembatan Timbang'),
  createData('4', 'inpos0001', 'Pos Jaga'),
  createData('5', 'inmrd0001', 'Jalan Utama'),
];

const headCells = [
  { id: 'id', numeric: false, disablePadding: true, label: 'Sequence' },
  { id: 'code', numeric: false, disablePadding: true, label: 'Code' },
  { id: 'title', numeric: false, disablePadding: true, label: 'Title' },
];

const datatheme='Infrastructures';

const useStyles = makeStyles({
    appHeader: {
      paddingLeft: '10px',
      paddingRight: '10px',
      width: '100%',
    },
    appMaster: {
      paddingLeft: '10px',
      paddingRight: '10px',
      width: '100%',
      maxHeight: '500px',
    },
    appDetail: {
      paddingLeft: '10px',
      width: '100%',
      verticalAlign: 'top',
    }
})

function Infrastructure() {
  const classes = useStyles()
  return (
    <>
      <Grid container className={classes.appDetail}>
          <Grid className={classes.appHeader} sm={6}>
          <GridPage rows={rows} headCells={headCells} datatheme={datatheme} />
          </Grid>
          <Grid className={classes.appHeader} sm={6}>
          </Grid>
      </Grid>
    </>
  )
}

export default Infrastructure;