import React from 'react'
import { Grid } from '@material-ui/core'
import { makeStyles } from '@material-ui/core';

import GridPage from '../GridPage'
//import Detail from '../Detail'

function createData(id, group, classname) {
  return { id, group, classname };
}

const rows = [
  createData('1', 'fleet', 'transport'),
  createData('2', 'fleet', 'heavy'),
  createData('3', 'infras', 'rooad'),
  createData('4', 'infras', 'bridge'),
  createData('5', 'infras', 'weightbridge'),
  createData('6', 'infras', 'post'),
  createData('7', 'land', 'enclave'),
  createData('8', 'land', 'plantation'),
  createData('9', 'tree', 'commodity'),
];

const headCells = [
  { id: 'id', numeric: false, disablePadding: true, label: 'Sequence' },
  { id: 'group', numeric: false, disablePadding: true, label: 'Group' },
  { id: 'class', numeric: false, disablePadding: true, label: 'Class' },
];

const datatheme='Varieties';

const useStyles = makeStyles({
    appHeader: {
      paddingLeft: '10px',
      paddingRight: '10px',
      width: '100%',
    },
    appMaster: {
      paddingLeft: '10px',
      paddingRight: '10px',
      width: '100%',
      maxHeight: '500px',
    },
    appDetail: {
      paddingLeft: '10px',
      width: '100%',
      verticalAlign: 'top',
    }
})

function AssetClass() {
  const classes = useStyles()
  return (
    <>
      <Grid container className={classes.appDetail}>
      <Grid className={classes.appHeader} sm={6}>
          <GridPage rows={rows} headCells={headCells} datatheme={datatheme} />
          </Grid>
          <Grid className={classes.appHeader} sm={6}>
          </Grid>
      </Grid>
    </>
  )
}

export default AssetClass;