import React from 'react'
import { Grid } from '@material-ui/core'
import { makeStyles } from '@material-ui/core';

import GridPage from '../../components/GridPage'
//import Detail from '../../components/Detail'

function createData(id, name) {
  return { id, name };
}

const rows = [
  createData('1', 'fleet'),
  createData('2', 'infras'),
  createData('3', 'land'),
  createData('4', 'tree'),
];

const headCells = [
  { id: 'id', numeric: false, disablePadding: true, label: 'Sequence' },
  { id: 'name', numeric: false, disablePadding: true, label: 'Group' },
];

const datatheme='Asset';

const useStyles = makeStyles({
  appHeader: {
    paddingLeft: '10px',
    paddingRight: '10px',
    width: '100%',
  },
  appMaster: {
    paddingLeft: '10px',
    paddingRight: '10px',
    width: '100%',
    maxHeight: '500px',
  },
  appDetail: {
    paddingLeft: '10px',
    width: '100%',
    verticalAlign: 'top',
  }
})

function AssetGroup() {
  const classes = useStyles()
  return (
    <>
      <Grid container className={classes.appDetail}>
          <Grid className={classes.appHeader} sm={6}>
          <GridPage rows={rows} headCells={headCells} datatheme={datatheme} />
          </Grid>
          <Grid className={classes.appHeader} sm={6}>
          </Grid>
      </Grid>
    </>
  )
}

export default AssetGroup;