import React from 'react'
import { Grid } from '@material-ui/core'
import { makeStyles } from '@material-ui/core';

import GridPage from '../GridPage'
//import Detail from '../Detail'

function createData(id, name, code, type) {
  return { id, name, code, type };
}

const rows = [
  createData('1', 'centimeter', 'cm', 'length'),
  createData('2', 'meter', 'm', 'length'),
  createData('3', 'kilometer', 'km', 'length'),
  createData('4', 'gram', 'gr', 'weight'),
  createData('5', 'kilogram', 'kg', 'weight'),
  createData('6', 'ton', 'ton', 'weight'),
  createData('7', 'meter square', 'm2', 'area'),
  createData('8', 'hectare', 'ha', 'area'),
  createData('9', 'kilometer square', 'km2', 'area'),
  createData('10', 'liter', 'lt', 'volume'),
  createData('11', 'kiloliter', 'kl', 'volume'),
  createData('12', 'meter cubic', 'm3', 'volume'),
  createData('13', 'fresh fruit brunch', 'ffb', 'quantity'),
  createData('14', 'unit', 'unit', 'quantity'),
  createData('15', 'package', 'pkg', 'quantity'),
  createData('16', 'tre', 'tree', 'quantity'),
];

const headCells = [
  { id: 'id', numeric: false, disablePadding: true, label: 'Sequence' },
  { id: 'name', numeric: false, disablePadding: true, label: 'Name' },
  { id: 'code', numeric: false, disablePadding: true, label: 'Code' },
  { id: 'type', numeric: false, disablePadding: true, label: 'Type' },
];

const datatheme='Measurement';

const useStyles = makeStyles({
    appHeader: {
      paddingLeft: '10px',
      paddingRight: '10px',
      width: '100%',
    },
    appMaster: {
      paddingLeft: '10px',
      paddingRight: '10px',
      width: '100%',
      maxHeight: '500px',
    },
    appDetail: {
      paddingLeft: '10px',
      width: '100%',
      verticalAlign: 'top',
    }
})

function UnitMesasurement() {
  const classes = useStyles()
  return (
    <>
      <Grid container className={classes.appDetail}>
          <Grid className={classes.appHeader} sm={6}>
          <GridPage rows={rows} headCells={headCells} datatheme={datatheme} />
          </Grid>
          <Grid className={classes.appHeader} sm={6}>
          </Grid>
      </Grid>
    </>
  )
}

export default UnitMesasurement;