import React from 'react'
import { Grid } from '@material-ui/core'
import { makeStyles } from '@material-ui/core';

import Sidemenu from '../../components/Sidemenu'
import Detail from '../../components/Detail'

const useStyles = makeStyles({
    appHeader: {
      paddingLeft: '10px',
      paddingRight: '10px',
      width: '100%',
    },
    appMaster: {
      paddingLeft: '10px',
      paddingRight: '10px',
      width: '100%',
      maxHeight: '500px',
    },
    appDetail: {
      paddingLeft: '10px',
      width: '100%',
      verticalAlign: 'top',
    }
})

function CultivationStandard() {
  const classes = useStyles()
  return (
    <>
      <Grid container className={classes.appDetail}>
          <Grid className={classes.appHeader} sm={3}>
          <Sidemenu />
          </Grid>
          <Grid className={classes.appHeader} sm={9}>
          <Detail />
          </Grid>
      </Grid>
    </>
  )
}

export default CultivationStandard;