import { Grid, makeStyles } from '@material-ui/core';
import React from 'react';
import { Planet } from 'react-planet';
//import { Router, Switch, Route } from "react-router-dom";

import MainMenuIcon from '@material-ui/icons/Apps';
import MasterDataIcon from '@material-ui/icons/Storage';
import CompanyAssetIcon from '@material-ui/icons/FolderSpecial';
import CultivationStandardIcon from '@material-ui/icons/Assignment';
import AssetDistributionIcon from '@material-ui/icons/AccountTree';
import DataTransactionIcon from '@material-ui/icons/Tune';

import CultureIcon from '@material-ui/icons/Nature';
import AssetIcon from '@material-ui/icons/Build';
import CategoryIcon from '@material-ui/icons/Category';
import MeasureIcon from '@material-ui/icons/FormatSize';

import InfraIcon from '@material-ui/icons/LocationCity';
import LandIcon from '@material-ui/icons/Business';
import TreeIcon from '@material-ui/icons/NaturePeople';
import FleetIcon from '@material-ui/icons/LocalShipping';


import '../index.css'
import history from '../history';
//import Products from "./ProductPage";
//import MasterData from "./MasterData";

const useStyles = makeStyles({
    appPlanet: {
      paddingLeft: '10px',
      paddingRight: '10px',
      justifyContent:"center",
      alignItems: "center:",

      width: '100%',
      height: '400px'
    }
  })

function App() {
    const classes = useStyles()
    return (
        <>
            <Grid container className={classes.appPlanet}>
            <Planet centerContent={
                <MainMenuIcon />
                }
                hideOrbit
                dragablePlanet
                dragRadiusPlanet={20}
                bounce            
                orbitRadius={150}
                //rotation={90}
                bounceDirection="BOTTOM"            
                autoClose
            >
                <Planet centerContent={
                    <MasterDataIcon />
                    }
                    open
                    dragablePlanet
                    dragRadiusPlanet={20}
                    bounce            
                    orbitRadius={70}
                    //rotation={30}
                    autoClose
                >
                    <Planet centerContent={
                        <CultureIcon />
                        }
                        open
                        dragablePlanet
                        dragRadiusPlanet={20}
                        bounce            
                        orbitRadius={40}
                        //rotation={30}
                        autoClose
                    >
                        <div 
                            onClick={() => history.push('/Commodity')}
                            title={"Commodity"}
                            style={{
                                height: 10,
                                width: 10,
                                borderRadius: '50%',
                                backgroundColor: '#88ad00',
                        }}/>
                        <div
                            onClick={() => history.push('/Variety')}
                            title={"Variety"}
                            style={{
                                height: 10,
                                width: 10,
                                borderRadius: '50%',
                                backgroundColor: '#ad0088',
                        }}/>
                    </Planet>
                    <Planet centerContent={
                        <AssetIcon />
                        }
                        open
                        dragablePlanet
                        dragRadiusPlanet={20}
                        bounce            
                        orbitRadius={40}
                        rotation={30}
                        autoClose
                    >
                        <div 
                            onClick={() => history.push('/AssetGroup')}
                            title={"Asset Group"}
                            style={{
                                height: 10,
                                width: 10,
                                borderRadius: '50%',
                                backgroundColor: '#88ad00',
                        }}/>
                        <div
                            onClick={() => history.push('/AssetClass')}
                            title={"Asset Class"}
                            style={{
                                height: 10,
                                width: 10,
                                borderRadius: '50%',
                                backgroundColor: '#ad0088',
                        }}/>
                        <div
                            onClick={() => history.push('/AssetStatus')}
                            title={"Asset Status"}
                            style={{
                                height: 10,
                                width: 10,
                                borderRadius: '50%',
                                backgroundColor: '#0088ad',
                        }}/>
                    </Planet>
                    <Planet centerContent={
                        <CategoryIcon />
                        }
                        open
                        dragablePlanet
                        dragRadiusPlanet={20}
                        bounce            
                        orbitRadius={40}
                        rotation={30}
                        autoClose
                    >
                        <div 
                            onClick={() => history.push('/Construction')}
                            title={"Construction"}
                            style={{
                                height: 10,
                                width: 10,
                                borderRadius: '50%',
                                backgroundColor: '#88ad00',
                        }}/>
                        <div
                            onClick={() => history.push('/Direction')}
                            title={"Direction"}
                            style={{
                                height: 10,
                                width: 10,
                                borderRadius: '50%',
                                backgroundColor: '#ad0088',
                        }}/>
                        <div
                            onClick={() => history.push('/SoilType')}
                            title={"Soil Type"}
                            style={{
                                height: 10,
                                width: 10,
                                borderRadius: '50%',
                                backgroundColor: '#0088ad',
                        }}/>
                    </Planet>
                    <Planet centerContent={
                        <MeasureIcon />
                        }
                        open
                        dragablePlanet
                        dragRadiusPlanet={20}
                        bounce            
                        orbitRadius={40}
                        //rotation={30}
                        autoClose
                    >
                        <div 
                            onClick={() => history.push('/UnitMesasurement')}
                            title={"Unit Mesasurement"}
                            style={{
                                height: 10,
                                width: 10,
                                borderRadius: '50%',
                                backgroundColor: '#88ad00',
                        }}/>
                        <div
                            onClick={() => history.push('/UnitConverter')}
                            title={"Unit Converter"}
                            style={{
                                height: 10,
                                width: 10,
                                borderRadius: '50%',
                                backgroundColor: '#ad0088',
                        }}/>
                    </Planet>
                </Planet>
                <Planet centerContent={
                    <CompanyAssetIcon />
                    }
                    open
                    dragablePlanet
                    dragRadiusPlanet={20}
                    bounce            
                    orbitRadius={70}
                    //rotation={30}
                    autoClose
                >
                    <div onClick={() => history.push('/Infrastructure')} title={"Infrastructure"}><InfraIcon /></div>
                    <div onClick={() => history.push('/Landstructure')} title={"Land Structure"}><LandIcon /></div>
                    <div onClick={() => history.push('/TreeManagement')} title={"Tree Management"}><TreeIcon /></div>
                    <div onClick={() => history.push('/Fleet')} title={"Fleet"}><FleetIcon /></div>
                </Planet>
                <div onClick={() => history.push('/CultivationStandard')} title={"Cultivation Standard"}><CultivationStandardIcon /></div>
                <div onClick={() => history.push('/AssetDistribution')} title={"Asset Distribution"}><AssetDistributionIcon /></div>
                <Planet centerContent={
                    <DataTransactionIcon />
                    }
                    open
                    dragablePlanet
                    dragRadiusPlanet={20}
                    bounce            
                    orbitRadius={70}
                    rotation={30}
                    autoClose
                >
                    <div onClick={() => history.push('/InfrastructureTransaction')} title={"Infrastructure"}><InfraIcon /></div>
                    <div onClick={() => history.push('/LandstructureTransaction')} title={"Land Structure"}><LandIcon /></div>
                    <div onClick={() => history.push('/TreeManagementTransaction')} title={"Tree Management"}><TreeIcon /></div>
                    <div onClick={() => history.push('/FleetTransaction')} title={"Fleet"}><FleetIcon /></div>
                </Planet>
            </Planet>
            </Grid>
        </>
	);
}

export default App;