import React from 'react'
import { Grid } from '@material-ui/core'
import { makeStyles } from '@material-ui/core';

import GridPage from '../GridPage'
//import Detail from '../Detail'

function createData(id, from, to, multi, type) {
  return { id, from, to, multi, type };
}

const rows = [
  createData('1', 'm', 'cm',100,'length'),
  createData('2', 'm', 'km',0.001,'length'),
  createData('3', 'g', 'kg',0.001,'weight'),
  createData('4', 'g', 'ton',1000,'weight'),
  createData('5', 'm2', 'ha',10000,'area'),
  createData('6', 'm2', 'km2',1000000,'area'),
  createData('7', 'l', 'kl',1000,'volume'),
  createData('8', 'l', 'm3',1000,'volume'),
];

const headCells = [
  { id: 'id', numeric: false, disablePadding: true, label: 'Sequence' },
  { id: 'from', numeric: false, disablePadding: true, label: 'From' },
  { id: 'to', numeric: false, disablePadding: true, label: 'To' },
  { id: 'multi', numeric: true, disablePadding: false, label: 'Multiplier' },
  { id: 'type', numeric: false, disablePadding: true, label: 'Type' },
];

const datatheme='Converter';

const useStyles = makeStyles({
    appHeader: {
      paddingLeft: '10px',
      paddingRight: '10px',
      width: '100%',
    },
    appMaster: {
      paddingLeft: '10px',
      paddingRight: '10px',
      width: '100%',
      maxHeight: '500px',
    },
    appDetail: {
      paddingLeft: '10px',
      width: '100%',
      verticalAlign: 'top',
    }
})

function UnitConverter() {
  const classes = useStyles()
  return (
    <>
      <Grid container className={classes.appDetail}>
          <Grid className={classes.appHeader} sm={6}>
          <GridPage rows={rows} headCells={headCells} datatheme={datatheme} />
          </Grid>
          <Grid className={classes.appHeader} sm={6}>
          </Grid>
      </Grid>
    </>
  )
}

export default UnitConverter;